
:root
{
  --yellow:#f5c32c;
  --orange:#fca61f;
  --black:#242049;
  --grey:#788097;
  --blueCard:#ddf8fe;
  --purple:rgb(238 210 255);
  --boxShadow:#0px 19px 60px rgba(0 0 0/8%);
 --orangeCard:rgba(252,166,31,0.45);
 --smboxShadow: -79px 51px 60px rgba(0,0,0,0.08);


}
.App{
  padding:0.5rem 3.5rem;
  overflow: hidden;
  color:var(--black);
}
.button{
  border-radius: 5px;
  border: 2px;
  padding: 12px;
  font: white;
  font-size: 20px;
  background: linear-gradient(180deg,#fdc50f 26.71%, #fb982f 99.36%);
  box-shadow: 0px 20px 24px 3px rgba(251,161,40,0.42);
  margin-left: 50px;
  margin-top: 20px;
  width: 100px;
}